























import { Component, Vue } from 'vue-property-decorator'
import { AppModule } from '@/store/modules/app'
import { UserModule } from '@/store/modules/user'
import Breadcrumb from '@/components/Breadcrumb/index.vue'
import Hamburger from '@/components/Hamburger/index.vue'
import { changStore } from '@/api/shopSet'
import { userLogout } from '@/api/logout'
import Cookies from 'js-cookie'

@Component({
  'name': 'Navbar',
  'components': {
    Breadcrumb,
    Hamburger
  }
})

export default class extends Vue {

  private storeId = this.getStoreId
  private restKey: number = 0

  get sidebar() {
    return AppModule.sidebar;
  }

  get device() {
    return AppModule.device.toString()
  }

  get userInfo() {
    return UserModule.userInfo
  }

  get name() {
    return (UserModule.userInfo as any).name ? (UserModule.userInfo as any).name : JSON.parse(Cookies.get('user_info') as any).name
  }

  get roles() {
    return UserModule.roles
  }

  get getStoreId() {
    let storeId = ''
    if (UserModule.storeId) {
      storeId = UserModule.storeId
    } else if ((UserModule.userInfo as any).stores != null) {
      storeId = (UserModule.userInfo as any).stores[0].storeId
    }
    return storeId
  }

  private toggleSideBar() {
    AppModule.ToggleSideBar(false)
  }

  private changHandle() {
    changStore({ 'storeId': this.storeId }).then(res => {
      if (res.data.code == 200 || res.data.status == 200) {
        UserModule.changeStore(res.data)
        if (this.$route.path == '/shopAdmin/about') {
          this.$router.go(0)
          // this.restKey++
        } else {
          this.$router.push('/shopAdmin/about')
        }
      } else {
        this.$message.error(res.data.message)
      }
    }).catch(err => {
      this.$message.error('请求出错了：' + err)
    })
  }

  private async logout() {
    Cookies.remove('token')
    userLogout({}).then((res: any) => {
      if (res && res.data && res.data.code === 1) {
        this.$router.push('/login')
      }
    })
    // this.$router.push(`/login?redirect=${this.$route.fullPath}`)
  }
}
