


































import { Component, Prop, Vue } from 'vue-property-decorator'
import { AppModule } from '@/store/modules/app'
import { UserModule } from '@/store/modules/user'
import SidebarItem from './SidebarItem.vue'
import variables from '@/styles/_variables.scss'
import { getSidebarStatus, setSidebarStatus } from '@/utils/cookies'

@Component({
  'name': 'SideBar',
  'components': {
    SidebarItem
  }
})
export default class extends Vue {
    get defOpen () {
    // const urlArr = this.$route.path.split('/')
    // const openStr = urlArr.length > 2 ? `/${urlArr[1]}` : '/'
      let path = ['/']
      this.routes.forEach((n:any, i:number) => {
        if (n.meta.roles && n.meta.roles[0] === this.roles[0]) {
          path.splice(0,1, n.path)
        }
      })
      return path
    }

    get defAct () {
      let path = '/'
      // if (this.roles[0] == 'shop' && this.$route.path == '/brand') {
      //   path = '/shopAdmin/member'
      // } else if (this.roles[0] == 'admin'&& this.$route.path == '/brand') {
      //   path = '/brand'
      // } else {
      //   path = this.$route.path
      // }
      return path
    }

    get sidebar () {
      return AppModule.sidebar
    }

    get roles () {
      return UserModule.roles
    }

    get routes () {
      let routes = JSON.parse(JSON.stringify([...(this.$router as any).options.routes]))
      let num:number[] = []
      routes.forEach((n: any, i:number) => {
        if (n.meta.roles && n.meta.roles[0] !== this.roles[0]) {
          num.push(i)
        }
      })
      num.forEach((n, i) => {
        routes.splice(n-i, 1)
      })
      return routes
    }

    get variables () {
      return variables
    }

    get isCollapse () {
      return !this.sidebar.opened
    }
  }
