import axios, { AxiosAdapter } from 'axios'
import { Message, MessageBox } from 'element-ui'
import { UserModule } from '@/store/modules/user'
import router from '@/router'

const service = axios.create({
  // 'baseURL': 'http://canzg-wsl.itheima.net:8081/', // 'http://161.189.152.255:8081/',  // 'http://canzg-wsl.itheima.net/enterpise', // https://mock.boxuegu.com/mock/872/', // 'http://172.16.43.139:8081/', // 'http://39.98.133.55:8081/', // https://mock.boxuegu.com/mock/872/', // process.env.VUE_APP_BASE_API,
  // baseURL: 'http://172.17.2.120:8080',
  baseURL: process.env.VUE_APP_BASE_API,
  // baseURL: '/api/api',
  'timeout': 5000
})

// Request interceptors
service.interceptors.request.use(
  (config: any) => {
  // config.data = config.params
  // Add X-Access-Token header to every request, you can add other custom headers here
    if (UserModule.token) {
      config.headers['authorization'] = UserModule.token
    } else if(UserModule.token && config.url != '/login') {
      window.location.href = '/login'
      return false
    }
    // config.headers['Access-Control-Allow-Origin'] = '*'
    // config.headers['Content-Type'] = 'application/json;'
    // get请求映射params参数
    if (config.method === 'get' && config.params) {
      let url = config.url + '?';
      for (const propName of Object.keys(config.params)) {
        const value = config.params[propName];
        var part = encodeURIComponent(propName) + '=';
        if (value !== null && typeof (value) !== 'undefined') {
          if (typeof value === 'object') {
            for (const key of Object.keys(value)) {
              let params = propName + '[' + key + ']';
              var subPart = encodeURIComponent(params) + '=';
              url += subPart + encodeURIComponent(value[key]) + '&';
            }
          } else {
            url += part + encodeURIComponent(value) + '&';
          }
        }
      }
      url = url.slice(0, -1);
      config.params = {};
      config.url = url;
    }
    return config
  },
  (error: any) => {
      Promise.reject(error)
  }
)

// Response interceptors
service.interceptors.response.use(
  (response: any) => {
    // if (response.data.code === 200 || response.data.status === 200){
    //   const res = response.data
    //   return response
    // }
    if (response.data.code === 0) {
      // Message.error(response.data.msg)
      response.data.msg === 'NOTLOGIN' && router.push('/login')
      // return window.location.href = '/login'
      // window.location.href = '/login'
      // return false
    } else if (response.data.code === 1) {
      // const res = response.data
      return response
    }
    return response
  },
  (error:any) => {
    Message({
      'message': error.message,
      'type': 'error',
      'duration': 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service
